export default function Entries({
  loading,
  entries,
  activeGroup,
  setLoading,
}: any) {
  return (
    <div
      className="foto-container"
      style={{ display: loading ? "none" : "block" }}
    >
      {entries
        .filter((entry: any) => entry.group === activeGroup)
        .map((entry: any, i: number) => {
          return (
            <img
              style={{ display: "none" }}
              key={entry.title}
              id={`${entry.title + i}`}
              alt={`${entry.title} is not loading for some reason`}
              src={
                entry.image.fields && entry.image.fields.file.url + "?w=1920"
              }
              onLoad={() => {
                setLoading(false);
                document.getElementById(`${entry.title + i}`)!.style.display =
                  "block";
              }}
            ></img>
          );
        })}
    </div>
  );
}
